import { HttpClient, NonNullablePaths } from '@wix/sdk-types';
import {
  ListBookmarksResponse,
  createBookmark as universalCreateBookmark,
  deleteBookmark as universalDeleteBookmark,
  listBookmarks as universalListBookmarks,
} from './events-schedule-v1-schedule-item-schedule-bookmarks.universal.js';

export const __metadata = { PACKAGE_NAME: '@wix/events' };

export function listBookmarks(httpClient: HttpClient): ListBookmarksSignature {
  return (eventId: string) =>
    universalListBookmarks(
      eventId,
      // @ts-ignore
      { httpClient }
    );
}

interface ListBookmarksSignature {
  /**
   * Retrieves a list of bookmarked schedule items for a current member.
   * @param - Event ID to which the schedule item belongs.
   */
  (eventId: string): Promise<
    NonNullablePaths<
      ListBookmarksResponse,
      | `items`
      | `items.${number}._id`
      | `items.${number}.hidden`
      | `items.${number}.name`
      | `items.${number}.description`
      | `items.${number}.stageName`
      | `items.${number}.status`
      | `items.${number}.eventId`
      | `items.${number}.draft`,
      4
    >
  >;
}

export function createBookmark(
  httpClient: HttpClient
): CreateBookmarkSignature {
  return (itemId: string, eventId: string) =>
    universalCreateBookmark(
      itemId,
      eventId,
      // @ts-ignore
      { httpClient }
    );
}

interface CreateBookmarkSignature {
  /**
   * Bookmarks a schedule item for a current member.
   * @param - Schedule item ID.
   * @param - Event ID to which the schedule item belongs.
   */
  (itemId: string, eventId: string): Promise<void>;
}

export function deleteBookmark(
  httpClient: HttpClient
): DeleteBookmarkSignature {
  return (itemId: string, eventId: string) =>
    universalDeleteBookmark(
      itemId,
      eventId,
      // @ts-ignore
      { httpClient }
    );
}

interface DeleteBookmarkSignature {
  /**
   * Removes a schedule item bookmark for a current member.
   * @param - Schedule item ID.
   * @param - Event ID to which the schedule item belongs.
   */
  (itemId: string, eventId: string): Promise<void>;
}

