const PACKAGE_NAME = '@wix/auto_sdk_events_schedule-bookmarks';

/** Retrieves a list of bookmarked schedule items for a current member. */
export function listBookmarks(payload: object): RequestOptionsFactory<any> {
  function __listBookmarks({ host }: any) {
    const metadata = {
      entityFqdn: 'wix.events.schedule.v1.schedule_item',
      method: 'GET' as any,
      methodFqn: 'wix.events.schedule.BookmarkManagement.ListBookmarks',
      packageName: PACKAGE_NAME,
      migrationOptions: {
        optInTransformResponse: true,
      },
      url: resolveWixEventsScheduleBookmarkManagementUrl({
        protoPath: '/v1/schedule/bookmarks',
        data: payload,
        host,
      }),
      params: toURLSearchParams(payload),
      transformResponse: (payload: any) =>
        transformPaths(payload, [
          {
            transformFn: transformRESTTimestampToSDKTimestamp,
            paths: [
              { path: 'items.createdDate' },
              { path: 'items.updatedDate' },
              { path: 'items.timeSlot.start' },
              { path: 'items.timeSlot.end' },
            ],
          },
        ]),
    };

    return metadata;
  }

  return __listBookmarks;
}

/** Bookmarks a schedule item for a current member. */
export function createBookmark(payload: object): RequestOptionsFactory<any> {
  function __createBookmark({ host }: any) {
    const metadata = {
      entityFqdn: 'wix.events.schedule.v1.schedule_item',
      method: 'POST' as any,
      methodFqn: 'wix.events.schedule.BookmarkManagement.CreateBookmark',
      packageName: PACKAGE_NAME,
      migrationOptions: {
        optInTransformResponse: true,
      },
      url: resolveWixEventsScheduleBookmarkManagementUrl({
        protoPath: '/v1/schedule/{itemId}/bookmark',
        data: payload,
        host,
      }),
      data: payload,
    };

    return metadata;
  }

  return __createBookmark;
}

/** Removes a schedule item bookmark for a current member. */
export function deleteBookmark(payload: object): RequestOptionsFactory<any> {
  function __deleteBookmark({ host }: any) {
    const metadata = {
      entityFqdn: 'wix.events.schedule.v1.schedule_item',
      method: 'DELETE' as any,
      methodFqn: 'wix.events.schedule.BookmarkManagement.DeleteBookmark',
      packageName: PACKAGE_NAME,
      migrationOptions: {
        optInTransformResponse: true,
      },
      url: resolveWixEventsScheduleBookmarkManagementUrl({
        protoPath: '/v1/schedule/{itemId}/bookmark',
        data: payload,
        host,
      }),
      params: toURLSearchParams(payload),
    };

    return metadata;
  }

  return __deleteBookmark;
}
